import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    baseUrl:'',
    //用户栏的状态 0代表处于登录页面 1代表处于注册页面 2代表忘记密码页面 3代表已登录
    userStatus:0,
    //用户信息
    userInfo:{
      src:'',
      username:''
    },
    isShowSideBar:false,
    isPhone:false
  },
  getters: {
  },
  mutations: {
    //更新用户栏状态
    updateUserStatus(state,value){
      state.userStatus=value
  
    },
 
    updateUserInfo(state,value){
      state.userInfo=value
    },
    updateIsShowSideBar(state){
      state.isShowSideBar=!state.isShowSideBar
    },
    updateIsShowisPhone(state,value){
      state.isPhone=value
    },
    updateBaseUrl(state,value){
      state.baseUrl=value
    }
  },
  actions: {
  },
  modules: {
  }
})
