//获取用户信息  user/getUserInfo  需要请求头
export function userGetUserInfoGet(token){
    return service({
        method:"GET",
        url:'user/getUserInfo',
        headers:{
            "Authorization ":token
        }
    })
}
//修改用户信息  user/modifyUserInfo
export function userModifyUserInfoPost(data,token){
    return service({
        method:"POST",
        url:'user/modifyUserInfo',
        data:data,
        headers:{
            "Authorization ":token
        }
    })
} 
//上传图片    1是用户图片  2是帖子图片
export function uploadLocalPost(data,token){
    return service({
        method:"POST",
        url:'uploadLocal',
        data:data,
        headers:{
            "Authorization ":token,
            "Content-Type": "multipart/form-data",
        }
    })
}
