        <!-- 侧边栏 -->
        <sideBar v-show="!isPhone"></sideBar>
        <sideBar v-show="isPhone && isShowSideBar"></sideBar>
        <div class="detail">
          <!-- 侧边栏对应内容 -->
          <keep-alive>
          <router-view></router-view>
          </keep-alive>
        </div>
        <div v-show="!isPhone" class="news"><newNav></newNav></div>
      </div>
    </div>
