<script>
import topNav from "@/components/top.vue";
import sideBar from "@/components/sideBar.vue";
import newNav from "@/components/newNav.vue";
import { mapMutations, mapState } from 'vuex';
import {userGetUserInfoGet} from '@/request/api/home'
import serviceObj from "@/request/index"
export default {
  components: {
    topNav,
    sideBar,
    newNav,
  },
  data() {
    return {
      isPhone: false,
      screenHeight: document.body.clientHeight, // 屏幕高度
      screenWidth: document.body.clientWidth, // 屏幕宽度
    };
  },
  computed:{
   ...mapState(["isShowSideBar","baseUrl"])
  },
  methods: {
    ...mapMutations(["updateIsShowisPhone","updateBaseUrl","updateUserInfo"]),
    setClient() {
      console.log(this.screenWidth); //可以打印考到窗口大小变化
      if (this.screenWidth <= 850) {
        console.log("我的手机！");
        this.isPhone = true;
        this.updateIsShowisPhone(true)
      }else{
        
      }
    },
  },
 async mounted() {
    console.log(serviceObj);
    console.log(serviceObj.baseURL);
    this.updateBaseUrl(serviceObj.baseURL)
    this.setClient();
    let token=localStorage.getItem('token')
    //证明没有token ,没登陆过
  if(token==null)return
  else{
     //获取用户信息
 let res = await userGetUserInfoGet(token)

 let result=res.data.data
  let data={
    username:result.username,
    age:result.age,
    sex:result.sex,
    src:this.baseUrl+result.usericon
  }
  this.updateUserInfo(data)
  }
  },

};
</script>
